@page { size: 180mm 235mm;
  marks: crop;
  margin: 20mm 18.65mm 15mm 18.65mm;
  counter-reset: footnote 0;
  counter-reset: page 0;
  @footnote { border-top: solid 1px #888888;
    float: bottom;
    font-size: 11px;
    margin: 0 24px; } }

@page :right { @bottom-right { margin-right: 1.5rem;
    content: counter(page);
    font-size: 11px; } }

@page :left { @bottom-left { margin-left: 1.5rem;
    content: counter(page);
    font-size: 11px; } }

.c-ref-desc { float: footnote; counter-increment: footnote; font-size: 10px; }

.c-ref-desc::footnote-call { color: red; display: none !important; font-size: 0 !important; }

.c-ref-desc::footnote-marker { content: counter(footnote, decimal-leading-zero); font-size: 8pt !important; margin: 0; font-weight: bold; font-style: normal; color: yellow; }

.o-p--tie { margin-top: -1.5rem; }

.o-p--tie:before { content: ''; display: inline-block; width: 2.25rem; }

.u-red { color: red; }

.c-ref__desc, .c-ref__href { position: relative; display: inline-block; font-family: "Graphik", sans-serif; font-size: 1rem; line-height: 1.5 ; }

@media print { .c-ref__desc, .c-ref__href { font-size: 0.6875rem; line-height: 1; } }

.c-ref__desc:hover:after, .c-ref__href:hover:after { visibility: visible; width: 100%; }

.c-ref__desc:after, .c-ref__href:after { content: ""; position: absolute; width: 0; height: 1px; bottom: 0; left: 0; background: #bbbbbb; visibility: hidden; transition: all 0.3s ease-in-out 0s; }

.c-kneenote, .c-ref { display: inline-block; }

.c-kneenote__short, .c-ref__short { color: #222; font-size: 0.75rem; line-height: 1; font-weight: 400; }

.c-kneenote__short-link, .c-ref__short-link { margin-left: 0.1875rem; }

.c-kneenote__excerpt, .c-ref__excerpt { display: none; }

.c-kneenote__type, .c-ref__type { color: #888888; padding-top: 0.4375rem; text-transform: uppercase; font-family: "Ocra", monospace; font-size: 12px; line-height: 1; font-size: 0.75rem; line-height: 1; text-align: left; display: inline-block; }

.c-kneenote__id, .c-ref__id { color: #222; font-size: 0.625rem; line-height: 1; font-weight: 400; }

.c-kneenote__desc, .c-kneenote-desc, .c-ref__desc, .c-ref-desc { margin-left: 0.1875rem; }

@media print { .c-kneenote__desc, .c-kneenote-desc, .c-ref__desc, .c-ref-desc { display: inline-block !important; visibility: visible !important; } }

.c-kneenote__strong, .c-ref__strong { font-weight: 700; text-transform: uppercase; font-size: 0.875rem; line-height: 1.71429 ; }

.c-kneenote--expand .c-ref__excerpt, .c-ref--expand .c-ref__excerpt { display: inline-block; }

.c-person { position: relative; margin: 3rem 0 1.5rem 0; page-break-inside: avoid; }

.c-person__image { position: absolute; top: 0; left: 0; width: 7.5rem; height: 7.5rem; border-radius: 50%; filter: grayscale(100%); object-fit: cover; }

.c-person__content { margin-left: 9rem; }

@media print { .c-proposal ul, .c-proposal ol, .c-proposal dl { font-size: 0.75rem; line-height: 2 ; } }

.c-proposal h3 { line-height: 1.4; }

.c-proposal h2 + p, .c-proposal h4 + p { margin-top: -0.75rem; }

.c-proposal img { margin-bottom: 1.5rem; }

.c-proposal .c-notes-marginal { margin: 1.5rem 0; }

.c-proposal .c-notes-marginal__item { padding-bottom: 0.375rem; }

.c-proposal .c-notes-marginal__item:last-child { padding-bottom: 0; }

@media (min-width: 79.0625em) { .c-proposal .c-notes-side-hook { display: block; position: absolute; top: 0; left: 0; width: 100%; } }

@media (min-width: 79.0625em) { .c-proposal .c-notes-side { position: absolute; left: calc(100% + 48px); width: 30%; } }

.c-proposal .c-notes-side__icon { display: block; }

@media (min-width: 79.0625em) { .c-proposal .c-note-side:after { content: "\00a0"; display: block; } }

.c-proposal .c-note-side__content { font-family: 'Graphik'; font-size: 0.875rem; line-height: 2; }

.c-proposal .c-note-side__headline { font-size: 1rem; line-height: 1.45; }

.c-proposal .c-note-side__text { display: block; }

.c-proposal .c-note-side__text:after { content: "\A\A"; }

.c-proposal .c-note-side__ref { font-weight: 700; }

.c-proposal p { font-family: "Medium", "Georgia", "Cambria", "Times New Roman", "Times", "serif"; letter-spacing: .01rem; font-weight: 400; font-style: normal; font-size: 21px; line-height: 1.58; letter-spacing: -.003em; position: relative; display: block; }

@media print { .c-proposal p { font-size: 13px; } }

.c-proposal blockquote { margin: 0 1.5rem 1.5rem 1.5rem; }

.c-proposal blockquote p { margin: 1.5rem 0 0.75rem 0; font-style: italic; font-weight: 300; }

.c-proposal .c-sources { border-top: 1px solid #c8c8c8; margin-top: 9rem; padding-top: 4.5rem; }

.c-proposal .c-sources p { font-family: "Graphik",sans-serif; font-size: 16px; line-height: 1.5; }

.c-proposal .c-sources h5 { margin-bottom: 0; }

.c-proposal .c-sources h4 { margin-bottom: 1.125rem; }

.c-proposal .c-sources a { color: #11448d; }

.c-proposal ul { margin-left: 1.5rem; margin-bottom: 3rem; list-style: circle; }

.c-proposal ul ul { margin-bottom: 0; }

.c-proposal ul li { padding-bottom: 0.1875rem; }

.c-proposal ul li:empty { display: none; }

.c-proposal ol { counter-reset: section; list-style-type: none; margin-bottom: 3rem; }

.c-proposal ol li { padding-left: 1.125rem; padding-bottom: 0.1875rem; position: relative; }

.c-proposal ol li:empty { display: none; }

.c-proposal ol li li { margin-left: 0.75rem; padding-left: 2.25rem; }

.c-proposal ol li li:before { width: 1.5rem; text-align: right; position: absolute; left: 0; top: 0.1875rem; font-family: "Ocra", monospace; font-size: 0.75rem; line-height: 2 ; counter-increment: section; content: counters(section, "."); }

.c-proposal ol li li li { padding-left: 3.1875rem; }

.c-proposal ol li li li li { padding-left: 4.125rem; }

.c-proposal ol ol { margin-bottom: 0; }

.c-proposal .c-li-lead:before { width: 0.5rem; text-align: right; position: absolute; left: 0; top: 0.1875rem; font-family: "Ocra", monospace; font-size: 0.75rem; line-height: 2 ; counter-increment: section; content: counters(section, "."); }

.c-proposal .c-li-lead__content { padding-bottom: 0.1875rem; }

.c-section-stage { position: relative; width: 100%; height: 31.5rem; margin: 4.5rem 0 3rem 0; }

.c-section-stage__background { border: 0; width: inherit; height: inherit; }

.c-section-stage__background--image { object-fit: cover; }

.c-section-stage__content { position: absolute; top: 50%; left: 50%; padding-left: 6rem; transform: translate(-50%, -20%); color: #fff; pointer-events: none; }

.c-section-stage__headline { width: 100%; font-size: 72px; line-height: 72px; display: block; white-space: nowrap; }

.c-section-stage__subline { width: 100%; display: block; }

.c-section-stage__id { position: absolute; left: 0; top: 0; font-family: "Ocra", monospace; font-size: 34px; line-height: 1.2; }

.c-section-stage__id:after { content: " / "; }

.c-video { position: relative; padding-top: 56.25%; width: 100%; overflow: hidden; page-break-inside: avoid; }

.c-video__item { position: absolute; top: 0; left: 0; width: 100%; height: 100%; border: 0; }

.c-video__overlay { display: none; }

@media print { .c-video__overlay { position: absolute; top: 0; left: 0; display: block; width: 100%; height: 100%; object-fit: cover; } }

.o-container-cases { margin: 0 auto; width: 100%; }

.o-container-small { margin: 0 auto; }

@media (max-width: 30em) { .o-container-small { width: 100%; padding: 0 1.5rem; } }

@media (min-width: 30.0625em) and (max-width: 46em) { .o-container-small { width: 100%; padding: 0 1.5rem; } }

@media (min-width: 46.0625em) and (max-width: 61em) { .o-container-small { width: 45em; } }

@media (min-width: 61.0625em) and (max-width: 90em) { .o-container-small { width: 45em; } }

@media (min-width: 90.0625em) { .o-container-small { width: 45em; } }

.o-container-large { margin: 0 auto; }

@media (max-width: 30em) { .o-container-large { width: 100%; padding: 0 1.5rem; } }

@media (min-width: 30.0625em) and (max-width: 46em) { .o-container-large { width: 100%; padding: 0 1.5rem; } }

@media (min-width: 46.0625em) and (max-width: 61em) { .o-container-large { width: 45em; } }

@media (min-width: 61.0625em) and (max-width: 90em) { .o-container-large { width: 60em; } }

@media (min-width: 90.0625em) { .o-container-large { width: 77.5em; } }

.o-container-media { margin: 0 auto; width: 100%; }

@media (min-width: 90.0625em) { .o-container-media { width: 77.5em; } }

.c-cases .o-grid__item { position: relative; height: 50vh; }

@media (min-width: 30.0625em) { .c-cases .o-grid__item { height: 100vh; } }

.c-case { display: block; position: absolute; left: 0; top: 0; background-size: cover; background-position: center; height: 100%; width: 100%; }

@media (min-width: 61.0625em) { .c-case:hover .c-case__title { transform: translateY(0); }
  .c-case:hover .c-case__excerpt { opacity: 1; height: auto; transform: translateY(0); }
  .c-case:hover .c-case__topic { transform: translateY(0); } }

.c-case__details { white-space: normal; position: absolute; width: 80%; left: 10%; color: #fff; height: auto; bottom: 17%; z-index: 999; transform: matrix(1, 0, 0, 1, 0, 0); }

.c-case__title { transition: 0.8s transform cubic-bezier(0.19, 1, 0.22, 1); transform: translateY(12px); font-weight: 700; font-size: 2.188rem; line-height: 2.625rem; margin: 0 0 1.875rem 0; letter-spacing: .02rem; pointer-events: none; margin-bottom: 24px; }

.c-case__excerpt { transition: all 1.5s cubic-bezier(0.19, 1, 0.22, 1); transform: translateY(100%); opacity: 0; height: 0; overflow: hidden; font-weight: 400; font-size: 1rem; line-height: 1.688rem; }

.c-case__topic { margin-top: 24px; font-weight: 700; font-size: .9375rem; line-height: 1.25rem; transition: 0.8s transform cubic-bezier(0.19, 1, 0.22, 1); transform: translateY(-12px); }

.c-cover { height: 120vh; width: 100%; position: relative; margin-bottom: 3rem; }

.c-cover__background { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-size: cover; background-position: center; background-repeat: no-repeat; }

.c-cover__headline { position: absolute; top: 40vh; left: 10%; max-width: 80%; color: #fbf9f6; display: block; font-family: "Graphik",sans-serif; font-weight: 700; font-size: 8vw; line-height: 1.1; letter-spacing: .02rem; }
